.mission {
  margin-bottom: 120px;
}

.mission__container {
  display: flex;
  justify-content: space-between;
  align-items: start;
}

.cards:last-child {
  font-weight: 700;
  font-size: 28px;
}

.mission__text {
  max-width: 65%;
  color: var(--dark-grey, #2F3338);
  font-size: 24px;
  font-weight: 400;
  line-height: 150%;
}

.mission__text span {
  font-weight: 700;
}

.mission__title_container {
  margin-right: 150px;
  margin-bottom: 20px;
}

@media (max-width: 1400px) {
.mission {
    margin-bottom: 10%;
  }
}
@media (max-width: 1080px) {
  .mission__text {
    font-size: 20px;
  }
}

@media (max-width: 870px) {
  .mission__text {
    font-size: 18px;
  }
}

@media (max-width: 540px) {
  .mission__text {
    font-size: 16px;
  }
}


@media (max-width:1280px) {
  .mission__container {
    flex-direction: column;
    align-items: stretch;
  }

  .mission__text {
    max-width: 100%;
  }
}